import CryptoJS from "crypto-js"

export function Codebook() {
  const smileysPrefix = "コ"
  const smileysEncryptCodebook = {
    a: "",
    b: "",
    c: "",
    d: "",
    e: "",
    f: "",
    g: "",
    h: "ぃ",
    i: "",
    j: "",
    k: "",
    l: "",
    m: "",
    n: "",
    o: "",
    p: "",
    q: "グ",
    r: "",
    s: "",
    t: "",
    u: "",
    v: "",
    w: "",
    x: "",
    y: "",
    z: "お",
    A: "",
    B: "",
    C: "",
    D: "ぉ",
    E: "",
    F: "",
    G: "",
    H: "",
    I: "",
    J: "",
    K: "",
    L: "",
    M: "",
    N: "",
    O: "",
    P: "",
    Q: "",
    R: "ズ",
    S: "",
    T: "",
    U: "",
    V: "",
    W: "",
    X: "が",
    Y: "く",
    Z: "",
    1: "サ",
    2: "",
    3: "",
    4: "",
    5: "",
    6: "",
    7: "",
    8: "",
    9: "",
    0: "き",
    "/": "か",
    "+": "ぅ",
    "=": "",
  }
  const smileysDecryptCodebook = {
    "": "a",
    "": "b",
    "": "c",
    "": "d",
    "": "e",
    "": "f",
    "": "g",
    "ぃ": "h",
    "": "i",
    "": "j",
    "": "k",
    "": "l",
    "": "m",
    "": "n",
    "": "o",
    "": "p",
    "グ": "q",
    "": "r",
    "": "s",
    "": "t",
    "": "u",
    "": "v",
    "": "w",
    "": "x",
    "": "y",
    "お": "z",
    "": "A",
    "": "B",
    "": "C",
    "ぉ": "D",
    "": "E",
    "": "F",
    "": "G",
    "": "H",
    "": "I",
    "": "J",
    "": "K",
    "": "L",
    "": "M",
    "": "N",
    "": "O",
    "": "P",
    "": "Q",
    "ズ": "R",
    "": "S",
    "": "T",
    "": "U",
    "": "V",
    "": "W",
    "が": "X",
    "く": "Y",
    "": "Z",
    "サ": "1",
    "": "2",
    "": "3",
    "": "4",
    "": "5",
    "": "6",
    "": "7",
    "": "8",
    "": "9",
    "き": "0",
    "か": "/",
    "ぅ": "+",
    "": "=",
  }

  function translate(str, codebook) {
    var newStr = ""
    for (const ch of str) {
      let newChar = codebook[ch]
      if (newChar !== null && newChar != undefined && newChar.length != 0) {
        newStr += newChar
      }
    }

    return newStr
  }

  const key = CryptoJS.enc.Utf8.parse("ohouohouohouohou") //浣杩舵颁涓哄
  const iv = CryptoJS.enc.Utf8.parse("ohou") //浣杩舵颁涓哄ュ绉婚

  //瑙ｅ规
  function decrypt(text) {
    let transText = translate(
      text,
      smileysDecryptCodebook
    )
    let decrypt = CryptoJS.AES.decrypt(transText, key, {
      iv: iv,
      mode: CryptoJS.mode.CBC,
      padding: CryptoJS.pad.Pkcs7,
    })
    let decryptedStr = decrypt.toString(CryptoJS.enc.Utf8)
    return decryptedStr.toString()
  }

  //瀵规
  function encrypt(text) {
    if (text.length == 0) return null
    let transText = CryptoJS.enc.Utf8.parse(text)
    let encrypted = CryptoJS.AES.encrypt(transText, key, {
      iv: iv,
      mode: CryptoJS.mode.CBC,
      padding: CryptoJS.pad.Pkcs7,
    })
    let pwdStr = encrypted.ciphertext.toString(CryptoJS.enc.Base64)

    return smileysPrefix + translate(pwdStr, smileysEncryptCodebook)
  }

  return {
    encrypt,
    decrypt
  }
}