<template>
  <h1>コ㈠ Emojiコ</h1>
  <div>
    <textarea
      v-model="state.fromStr"
      placeholder="杈ラ瑕瀵瀹"
      autofocus="true"
    ></textarea>
  </div>

  <div class="btns">
    <div><button v-on:click="encryptText">瀵猬锔</button> <br /></div>
    <div><button v-on:click="decryptText">瑙ｅ猬锔</button> <br /></div>
  </div>

  <div>
    <textarea
      v-model="state.toStr"
      placeholder="杈ラ瑕瑙ｅEmoji"
    ></textarea>
  </div>
</template>

<script>
import { reactive } from "vue"
import { Codebook } from "./Codebook.js"

export default {
  setup() {
    const state = reactive({
      fromStr: "",
      toStr: "",
    })

    const {
      encrypt,
      decrypt
    } = Codebook()

    //瑙ｅ规
    function decryptText() {
      state.fromStr = decrypt(state.toStr)
    }

    //瀵规
    function encryptText() {
      if (state.fromStr.length == 0) return
      state.toStr = encrypt(state.fromStr)
    }

    return {
      state,
      encryptText,
      decryptText,
    }
  },
}
</script>

<style>
textarea {
  width: 100%;
  height: 150px;
}

.btns {
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>